import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)


import index from './routes/index.vue'
import car from './routes/car.vue'

var routes=[
	{
		path:'/',
		name:'index',
		component:index
	},
	{
		path:'/car',
		name:'car',
		component:car
	},
	
]

export default new Router({
	routes
})


