import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)


//相当于data
var state = {
	arr: [],
	name: ''
}


//相当于计算属性
var getters = {
	//去重  种类
	c_g_arr(state) {
		var newArr = [];
		for (var i = 0; i < state.arr.length; i++) {
			if (newArr.indexOf(state.arr[i].type) == -1) {
				newArr.push(state.arr[i].type)
			}
		}
		return newArr
	},
	//计算总价
	z_price() {
		var z_num = 0;
		for (var i = 0; i < state.arr.length; i++) {
			z_num += state.arr[i].price * state.arr[i].s
		}
		return z_num
	},


}


//唯一可以进行修改的地方  对内不对外
var mutations = {
	mut_arr(state, num) {
		//加入购物车
		var type = true;
		for (var i = 0; i < state.arr.length; i++) {
			if (state.arr[i].name == num.name) {
				type = false
				state.arr[i].s++
			}
		}
		if (type) {
			num.s = 1
			state.arr.push(num)
			state.name = num.name
		}
	},
	add_arr(state, add_num) {
		//数量++
		for (var i = 0; i < state.arr.length; i++) {
			if (state.arr[i].name == add_num) {
				state.arr[i].s++
				console.log(state.arr[i].s)
			}
		}
	},
	del_arr(state, del_num) {
		//数量--
		for (var i = 0; i < state.arr.length; i++) {
			if (state.arr[i].name == del_num) {
				if (state.arr[i].s > 1) {
					state.arr[i].s--
				} else {
					alert('数量不能为零，请直接删除')
				}
				console.log(state.arr[i].s)
			}
		}
	},
	remove_arr(state, remove_num) {
		//删除数据
		for (var i = 0; i < state.arr.length; i++) {
			if (state.arr[i].name == remove_num) {
				state.arr.splice(i, 1)
			}
		}
	},


}



//接收参数 对外不对内
var actions = {
	act_arr({
		commit
	}, num) {
		commit('mut_arr', num)
		location.href = "#/car"
	},
	add({
		commit
	}, add_num) {
		commit('add_arr', add_num)
	},
	del({
		commit
	}, del_num) {
		commit('del_arr', del_num)
	},
	remove({
		commit
	}, remove_num) {
		commit('remove_arr', remove_num)
	},

}



export default new Vuex.Store({
	state,
	getters,
	mutations,
	actions,
})



