<template>
	<div id="app">
		<div class="top">
			<a @click="index=$index" :href="i.path" v-for="(i,$index) in arr_name" :style="index==$index?{'color':'red'}:{'color':'#000'}">{{i.name}}</a>
		</div>

		<div style="text-align: center;padding: 0px 0px;">
			<router-view></router-view>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'app',
		data() {
			return {
				index: 0,
				arr_name: [{
						name: '商城',
						path: '#/'
					},
					{
						name: '🛒',
						path: '#/car'
					}
				]
			}
		},
		created() {}
	}
</script>

<style>
	* {
		margin: 0px;
		padding: 0px;
	}

	.top {
		height: 60px;
		line-height: 60px;
		border-bottom: 1px solid #ccc;
		text-align: center;
		display: flex;
	}

	a {
		text-decoration: none;
		color: black;
		padding: 0 20px;
	}

	a:nth-child(1) {
		flex: 12;
		text-align: left;
		margin-left: 20px;
	}

	a:nth-child(2) {
		flex: 1;
	}
</style>



