<template>
	<div>
		<div class="t_cont" style="padding-top: 20px;">
			<div v-if="$store.state.arr.length==0" style="padding-top: 20px;width: 100%;">
				<p>购物车暂时没有数据 <a href="#/" style="color:yellowgreen">点击这里返回商城</a></p>
			</div>
			<div v-else>
				<div class="already">
					<strong>√</strong> “{{$store.state.name}}” 成功添加进您的购物车. <a href="#/">继续购物 </a> 
				</div> 
				<div style="">
					<div class="c_body">
							<ul class="p_tit" v-if="$store.state.arr.length">
								<li class="p_li" style="flex: 2.8;text-align: left;padding-left: 10px;">商品</li>
								<li class="p_li"style="flex: 1.5;" >价格</li>
								<li class="p_li" style="flex: 2;">数量</li>
								<li class="p_li">合计</li>
							</ul>
							<div class="msg">
								<div v-for="i in c_g_arr">
									<h4 style="width: 100%;text-align: left;padding-left: 10px;">{{i}}</h4>
									<ul class="m_ul" v-for="a in $store.state.arr" v-if="i == a.type" style="display: flex;">
										<li class="p_l_shop" @click="remove(a.name)" style=";font-size: 30px;flex: 0.2">
											<span style="width: 18px;height: 18px;border-radius: 50%;border:2px solid #ddd;display: inline-block;font-size: 12px;line-height:12px;text-align:center;background: #fff;
											color: #ddd;font-weight: 800;">×</span>
										</li>
										<li class="p_l_shop" style="flex: 1.8;position: relative;text-align: left;padding-left: 45px;">
											<img :src="a.img" alt="" style="width: 90px;height: 90px;">
											<span style="position: absolute;top: 40px;left: 150px;color: #519f10;">{{a.name}}</span>
										</li>
										<li class="p_l_shop" style="">{{a.price}}</li>
										<li class="p_l_shop" style="margin-left: 80px;margin-right: 10px;">
											<span class="_pnum" @click="del(a.name)">-</span>
											<span class="_pnum" >{{a.s}}</span>
											<span class="_pnum" @click="add(a.name)">+</span>
										</li>
										<li class="p_l_shop" style="">{{a.price*a.s}}</li>
									</ul>
								</div>
							</div>
					</div>
					<div class="right" >
						<p style="font-size: 16px;color: #555;text-align: left;line-height: 40px;border-bottom: 2px solid #878787;font-weight: bold;">购物车总计</p>
						<div style="display: flex;line-height: 40px; border-bottom:1px solid #ddd">
							<p style="flex: 5;text-align: left;font-size: 14px;color: #777;font-weight: bold;">购物车小计</p>
							<p style="flex: 1;font-size: 14px;color: #777;">&yen;{{z_price}}</p>
						</div>
						<div style="display: flex;line-height: 40px; border-bottom:1px solid #ddd">
							<p style="flex: 5;text-align: left;font-size: 14px;color: #777;font-weight: bold;">运费</p>
							<p style="flex: 1;font-size: 14px;color: #777;">免费送货</p>
						</div>
						<div style="display: flex;line-height: 40px; border-bottom:2px solid #ddd;margin-bottom: 20px;">
							<p style="flex: 5;text-align: left;font-size: 14px;color: #777;font-weight: bold;">订单总计</p>
							<p style="flex: 1;font-size: 14px;color: #777;">&yen;{{z_price}}</p>
						</div>
						<div style="background: #519f10;height: 42px;line-height: 42px;color: #fff;margin-bottom: 20px;">前往收银台</div>
						
						<div>
							<p style="font-size: 16px;color: #555;text-align: left;line-height: 40px;border-bottom: 2px solid #ccc;font-weight: bold;">优惠券:</p>
							<input type="text" placeholder="优惠券代码" style="width:100%;padding: 7px 10px;margin: 15px 0 15px;height: 40px;outline: none;font-size: 17px;">
						</div>
						
						<div style="background: #90af6b;height: 42px;line-height: 42px;color: #fff;margin-bottom: 20px;">前往收银台</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import {
		mapGetters,
		mapActions
	} from 'vuex'
	export default {
		computed: {
			...mapGetters(['c_g_arr','z_price'])
		},
		methods: {
			...mapActions(['add', 'del','remove'])
		}


	}
</script>

<style>
	.right{
		margin-left: 880px;
		min-height: 300px;
		/* background: lightblue; */
		padding: 0 20px;
		width: 400px;
	}
	.already {
		border: 1px solid #88dfae;
		font-size: 16px;
		font-weight: normal;
		color: #2f9a4c;
		padding: 15px 8px 15px 20px;
		position: relative;
		background: #ffffff;
		margin-bottom: 20px;
	}

	.already a {
		color: #ffffff;
		border-color: transparent;
		background-image: none;
		background-color: #10ac62;
		font-size: 14px;
		font-weight: normal;
		padding: 10px 20px;
		position: absolute;
		right: 5px;
		top: 5.5px;
	}
	.c_body{
		width: 860px;
		float: left;
		border-right: 1px solid #ccc;
	}
	.m_ul{
		width: 860px;
		border-bottom: 1px solid #ccc;
		padding: 10px;
	}
	.p_tit {
		display: flex;
		width: 860px;
		/* background: lightblue; */
		border-bottom: 2px solid #ccc;
	}

	.msg {
		/* display: flex; */
		width: 750px;
		
		/* background: lightblue; */
	}

	.p_li {
		flex: 1;
		text-align: center;
		line-height: 60px;
		padding-top: 10px;
	}

	.p_l_shop {
		flex: 1;
	}
	
	._pnum {
		border: 1px solid #ccc;
		display: inline-block;
		width: 30px;
		height: 30px;
		line-height: 30px;
		cursor: pointer;
		-webkit-user-select: none;
	}
</style>



